"use client";

import { useState, useEffect, useRef } from "react";
import { useRouter } from "next/navigation";
import { ArrowBigRightDash } from "lucide-react";
import Link from "next/link";
import axios from "axios";

function Page() {
  const [img, setImg] = useState(null);
  const [spendings, setSpendings] = useState([]); // Store fetched spendings
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const router = useRouter();
  const finBuddyRef = useRef(null);

  useEffect(() => {
    const storedImg = localStorage.getItem("photo");
    setImg(storedImg);

    const user = localStorage.getItem("user");
    if (!user) {
      router.push("/");
    }
  }, [router]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.post("http://10.50.0.89:8888/api/spendings/", {
          user_uid: "PkrrbhFWbGPISmVAGivoCYpsuGb2",
        });

        console.log("Response Data:", response.data);
        setSpendings(response.data.spendings || []); // Assuming the data has a "spendings" array
      } catch (err) {
        console.error("Error fetching data:", err);
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  return (
    <div className="h-screen w-full text-blue-600">
      {/* Navbar */}
      <nav className="fixed top-0 h-20 w-full flex items-center justify-between px-10 gap-10">
        <div>
          <h1 className="text-3xl">FinBuddy</h1>
        </div>
        <div className="flex gap-10 items-center justify-center">
          <Link href="/dashboard">
            <h1 className="cursor-pointer">Home</h1>
          </Link>
          <Link href="/spending">
            <h1>Spendings</h1>
          </Link>
          <Link href="/forecast">
            <h1>Forecast</h1>
          </Link>
          <Link href="/">
            <h1>Suggestion</h1>
          </Link>
          <div className="h-10 w-10 rounded-full overflow-hidden">
            <img src={img || ""} alt="User" />
          </div>
        </div>
      </nav>

      {/* Spendings Section */}
      <div className="h-full w-full py-20 px-10">
        <h1 className="text-5xl text-blue-600 flex items-center gap-1">
          Spendings
          <ArrowBigRightDash size={70} />
        </h1>

        {/* Show Loading or Error */}
        {loading ? (
          <p className="text-xl mt-5">Loading spendings...</p>
        ) : error ? (
          <p className="text-red-500 mt-5">Error: {error}</p>
        ) : (
          <div className="h-[30rem] w-full rounded-3xl mt-2 border-2">
            <div className="p-3 flex rounded-t-3xl bg-gray-200 font-bold">
              <div className="w-1/4 border-r-2 px-4">Description</div>
              <div className="w-1/4 border-r-2 px-4">Date</div>
              <div className="w-1/4 border-r-2 px-4">₹Amount</div>
              <div className="w-1/4 px-4">Category</div>
            </div>

            {/* Dynamically Render Spendings */}
            {spendings.length > 0 ? (
              spendings.map((item, index) => (
                <div
                  key={index}
                  className="p-3 flex border-b-2 border-gray-300 bg-white hover:bg-gray-100"
                >
                  <div className="w-1/4 border-r-2 px-4">{item.description}</div>
                  <div className="w-1/4 border-r-2 px-4">{item.date}</div>
                  <div className="w-1/4 border-r-2 px-4">₹{item.amount}</div>
                  <div className="w-1/4 px-4">{item.category}</div>
                </div>
              ))
            ) : (
              <p className="p-5 text-center text-gray-500">No spendings found.</p>
            )}
          </div>
        )}
      </div>
    </div>
  );
}

export default Page;
